* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
}

a {
  color: #333;
  text-decoration: none;
}

.container {
  padding: 0 1rem;
}

.btn {
  display: inline-block;
  border: none;
  background: #555;
  color: #fff;
  padding: 7px 20px;
  cursor: pointer;
}

.btn:hover {
  background: #666;
}

body{
background: rgb(214,219,191); /* Old browsers */ /* FF3.6-15 */
background: -webkit-linear-gradient(top,  rgba(214,219,191,1) 56%,rgba(214,219,191,1) 100%,rgba(254,255,232,1) 100%,rgba(214,219,191,1) 101%); /* Chrome10-25,Safari5.1-6 */
background: linear-gradient(to bottom,  rgba(214,219,191,1) 56%,rgba(214,219,191,1) 100%,rgba(254,255,232,1) 100%,rgba(214,219,191,1) 101%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#d6dbbf', endColorstr='#d6dbbf',GradientType=0 ); /* IE6-9 */
}
